import Toast from 'wxcomponents/vant/dist/toast/toast';
import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			banner: [],//轮播图
			id: '',//记录传过来的商品id
			collection: '',//商品收藏状态
			product: '',//传过来的商品信息
			show: false,//控制弹层的显示隐藏
			bannerIndex:1,//轮播图下标
			count: 1,//记录商品数量
			Operations: '',//用于区别点击的是发起拼单还是单独购买
			newsList: [],//记录新品页面信息
			elasticLayer:{},//记录商品规格的对象
			obj:{},//记录需要向后台传的内容
			goods_attr:''//接收商品属性的字段（传给后台）
		}
	},
	methods: {
		goBack() {
			uni.navigateBack()
		},
		changeImg(e){
			this.bannerIndex=e.detail.current+1
		},
		isShow(e) {
			this.show = true
			// console.log(e.currentTarget.dataset.index)
			this.Operations = e.currentTarget.dataset.index
		},
		onClose() {
			this.show = false
		},
		// 规格选择
		selectSize(key,index,value) {
			this.$set(this.obj,key,value)
			this.elasticLayer.attr_group_data[key].curIndex=index
			this.goods_attr=this.obj[0]+','+this.obj[1]
			// console.log(this.elasticLayer.attr_group_data[0].attr_data[index].attr_value)
			// console.log(this.elasticLayer.attr_group_data[1].attr_data[index].attr_value)
		},
		// 加号按钮
		add() {
			this.count++
		},
		// 减号按钮
		cutBack() {
			this.count--
			if (this.count <= 1) {
				this.count = 1
			}
		},
		// 跳转首页
		goIndex() {
			uni.switchTab({
				url: '/pages/index/index'
			})
		},
		// 商品收藏  
		collect(e) {
			this.collection = !this.collection
			var arr = this.newsList
			for (var i = 0; i < arr.length; i++) {
				if (arr[i].goods_id == e.currentTarget.dataset.id) {
					arr[i].collect = this.collection
					uni.setStorage({
						key: 'collectList',
						data: JSON.stringify(arr)
					})
				}
			}
		},
		// 确定按钮(加入购物车功能)
		goShop() {
			// 判断是否有商品规格选项
			if(this.elasticLayer.attr_group_data){
				// 判断商品规格是否全部选中
				var flag=this.elasticLayer.attr_group_data.every((item,index)=>{
					return item.curIndex==0||item.curIndex>0
				})
			}else{
				var flag=true
			}
			if (flag) {
				if (this.Operations == 0) {
					// 点击了发起拼单
					console.log(this.product)
					var obj = {
						total_num:this.count,
						total_price:this.count*this.product.shop_price,
						goods_id:this.product.goods_id
					}
					console.log(obj)
					wx.navigateTo({
						url: '/pages/settlement/settlement?operations=0&obj='+JSON.stringify(obj)
					})
				} else {
					// 单独购买
					uni.request({
						url:baseUrl+'cart/addCart',
						method:'POST',
						data:{
							goods_id:this.product.goods_id,
							goods_name:this.product.goods_name,
							market_price:this.product.market_price,
							goods_price:this.product.shop_price,
							goods_number:this.count,
							goods_attr:this.goods_attr
						},
						success(e){
							console.log(e.data)
						}
					})
					wx.switchTab({
						url: '/pages/shop/shop'
					})
				}
			} else {
				Toast('请选择商品类型');
			}
		},
	},
	onLoad(options) {
		var _this = this
		this.product = JSON.parse(options.item)
		this.id = this.product.goods_id
		console.log(this.id)
		uni.request({
			url:baseUrl+"product/detail",
			method:'POST',
			data:{'goods_id':this.id},
			success(res){
				console.log(res.data.data.goods)
				_this.banner=res.data.data.gallery
				_this.product=res.data.data.goods
			}
		})
		uni.request({
			url:baseUrl+"product/GetGoodsSku",
			method:'POST',
			data:{'goods_id':this.id},
			success(res){
				console.log(res.data.data)
				_this.elasticLayer=res.data.data
			}
		})
	},
	onShow() {
		var that = this
		// 判断是否已收藏该id下的商品
		uni.getStorage({
			key: 'collectList',
			success(e) {
				// console.log(JSON.parse(e.data))
				that.newsList = JSON.parse(e.data) || []
				// console.log(that.newsList)
				for (var i = 0; i < that.newsList.length; i++) {
					if (that.newsList[i].goods_id == that.id) {
						that.collection = that.newsList[i].collect
					}
				}
			}
		})
	}
}
